@mixin on-tablet() {
  @media (min-width: $tablets-min-width) {
    @content;
  }
}

@mixin on-desktop() {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-inline() {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin page-grid() {
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}

@mixin footer-image-position() {
  height: 320px;
  width: 100%;

  @include on-tablet {
    height: 560px;
    width: 100%;
  }

  @include on-desktop {
    height: 100vh;
    width: 100%;
  }
}
